@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&family=Poppins:wght@200;300;400&display=swap');

body,html{
    font-family: 'Poppins';
    background: #141414;
    scroll-behavior: smooth !important;
}
.spinner-div{
    margin: 4rem 0;
    z-index: 100000;
    display: flex;
    justify-content: center;
    align-items: center;
    color: red !important;
}
#scrollTop{
    position:fixed;
    bottom:1rem;
    right: 1rem;
    color: white;
    padding: .2rem .5rem;
    cursor: pointer;
    height: 2.5rem;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(255,0,0,1);
    background: rgba(255,0,0,1);
    transition: .2s ease;
}
#scrollTop:hover{
    background: rgba(255,0,0,.5);
}
.modal{
    z-index: 10000000000000000000;
}
.modal-content{
    border: 1px solid red;
    background: rgba(0,0,0,0.8) !important;
    color: #fff;
    backdrop-filter: blur(4px);
}
.modal iframe{
    border: 1.5px solid white;
    border-radius: 1rem;
    width: 100%;
    margin: 1rem 0px;
    height: 231px
}
.modal .img-poster{
    width: 100%;
    margin-bottom: 1rem;
}
.modal .genres{
    display: flex;
    justify-self: center;
    align-items: center;
    flex-wrap: wrap;
}
.modal .genres p{
    background-color: rgba(255,0,0,.25);
    border: 1px solid red;
    padding: .2rem;
    margin-right: .5rem;
    font-size: .8rem;
    border-radius: 4px;
}
.modal .title{
    margin: 1rem 0;
}
.modal .production{
    display: flex;
    flex-wrap: wrap;
}
.modal .production p{
    font-size: .9rem;
    border: 1px solid rgba(255,255,255,0.3);
    padding: .5rem .8rem;
    border-radius: 1rem;
    margin-right: .5rem;
}
.modal .btn-modal-close{
    background-color: red;
    color: white;
    border: 1.5px solid red;
    transition: .2s ease;
}
.modal .btn-modal-close:hover{
    background-color: transparent;
}
.navbar-collapse{
    display: flex;
    justify-content: flex-end;
}

.navbar-toggler-icon{
    background: none!important;
    position: relative;
}
.navbar-toggler-icon::before{
    content: '';
    position: absolute;
    top: 10px;
    left: 10px;
    width: 20px;
    height: 2px;
    background-color: red;
    border-radius: 6px;
    transition: .2s ease;
}
.navbar-toggler-icon::after{
    content: '';
    position: absolute;
    top: 18px;
    left: 10px;
    width: 12px;
    height: 2px;
    background-color: red;
    border-radius: 6px;
    transition: .2s ease;
}
.navbar-toggler-icon:hover::after,.navbar-toggler-icon:hover::before{
    width: 20px;
}
.trending-btn{
    color: red;
    margin: 0 1rem;
}
.trending-btn:hover{
    color: rgba(255,0,0,.8);
}
.trending-btn:focus{
    transform: none !important;
}
.dropdown-btn{
    background-color: red;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid red;
}
.dropdown-btn:hover,.dropdown-btn:active,.dropdown-btn:focus{
    transform: none !important;
    color: white;
    background-color: transparent;
}
.dropdown-menu{
    background: rgba(0,0,0,0.95);
    backdrop-filter: blur(10px);
}
.dropdown-item{
    color: white;
}
.dropdown-item:hover{
    background-color: red;
    color: white;
}
.main-container{
    padding: 2rem;
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.card{
    margin: 1rem;
    position: relative;
    background-color:transparent !important;
    transition: .2s ease;
    border-radius: 6px;
    min-width: 200px;
    /* overflow: hidden; */
}
.navbar{
    background: rgb(15,15,15,0.8) !important;
    position: fixed;
    top: 0;
    z-index: 10000;
    width: 100%;
    backdrop-filter: blur(3px);
}
.navbar .navbar-brand{
    color: red;
    text-transform: uppercase;
    font-weight: 400;
    margin-right: auto;
}
.navbar .navbar-brand:hover{
    color: red;
    opacity: .6;
}
.navbar form{
    border-radius: 6px;
    border: 1px solid rgba(255,0,0,0.5);
    color: white;
}
.navbar form:hover{
    border: 1px solid rgba(255,0,0,.8);
}
.navbar input.form-control{
    background: transparent;
    border: none;
    box-shadow: none;
    color: rgba(255,255,255,0.8);
}
.navbar button{
    display: flex;
    justify-content: center;
    box-shadow: none;
}
.navbar button:focus,.navbar button:active{
    box-shadow: none;
    transform: scale(.8);
}
.navbar button svg{
    color: white;
    opacity: .5;
}
.navbar button svg:hover{
    opacity: 1;
}


.card img{
    border-radius: 6px;
}
.card .tag{
    height: 2.2rem;
    width: 2.2rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: rgb(20,20,20);
    padding: 1.1rem;
    border: 1.5px solid red;
    color: white;
    right: 0;
    top: 0;
    transform: translate(50%,-50%);
    z-index: 100;
}
.card .tag p{
    margin: 0;
    font-size: .8rem;
    color: rgba(255,255,255,0.8);
}
.card .overview{
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 6px;
    border: none;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.8);
    padding: .8rem;
    color: white;
    opacity: 0;
    font-size: .8rem;
    pointer-events: none;
    backdrop-filter: blur(1px);
    transition: .2s linear;
}
.know-more{
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000000000000000000;
    position: absolute;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    opacity: 0;
    pointer-events: none;
    box-shadow: none;
    border: 1px solid red;
    transition: .2s ease;
}
.know-more:hover{
    background-color: rgba(255,0,0,.25);
    box-shadow: none;
    text-decoration: underline;
}
.card:hover .know-more{
    opacity: 1;
    color: white;
    box-shadow: none;
    pointer-events: all;
}
.card .info{
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-radius: 0 0 6px 6px;
    border: none;
    height: auto;
    /* min-height: 5rem; */
    width: 100%;
    background:rgba(0,0,0,0.8);
    padding: .8rem;
    color: white;
    /* opacity: 0; */
    pointer-events: none;
    backdrop-filter: blur(4px);
    transition: .2s linear;
    overflow: hidden;
}
.card .info p{
    font-size: 0.8rem;
    color: rgba(255,255,255,0.5);
    font-weight: 600;

}

.card:hover {
    border-radius: 6px;
    border: 2px solid red;
    transform: translateY(-10px);
    background-color: transparents;
}

.card:hover .overview{
    opacity: 1;
    left: 0 !important;
}
.card:hover .info{
    opacity: 0;
}
.card .card-title{
    margin-bottom: 0.3rem !important;
}
.load{
    border: 1px solid red;
    color: red;
    min-width: 15rem;
    margin-top: 2rem;
    box-shadow: none;
    transition: .2s ease;
}
.load:hover,.load:focus{
    color: red;
    box-shadow: none;
    background: rgba(255,0,0,0.05);
}
@media screen and (max-width:768px) {
    .navbar-collapse{
        flex-direction: column;
        justify-content: center;
    }
    .trending-btn{
        padding: 0;
        margin: 1rem 0;
    }
    .navbar form input{
        width: 80%;
    }
}